import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
	{
		name:'index',
		path:"/",
		component:resolve=>require(['components/home.vue'],resolve)
	},
  	{
  		name:'404',
  		path:"*",
  		component:resolve=>require(['components/404.vue'],resolve)
  	}
  ]
})


